	import TabGroup from '../group/tab'
	import {getMyTeamList} from "@/api/user"
	export default{
		data(){
			return{
               title:"我的团队",
               params:{
               	pageNum:1,
               	pageSize:20,
               	userId:''
               },
               teamList:'',
               total:0
			}
		},
		mounted(){
      this.params.userId= this.$store.getters.userInfo.userId
      this.getMyTeamList();
       // if(this.$store.getters.userInfo.level>0){
       //   this.getMyTeamList();
       // }else{
       //  this.$router.push('./addhui')
       // }
		},
		components:{
          TabGroup
		},
		filters:{
         momment(a){
          let b="";
      b=new Date(a).getFullYear()+'-'+new Date(a).getMonth()+'-'+new Date(a).getDate()+' '+new Date(a).getHours()+':'+new Date(a).getMinutes()+':'+new Date(a).getSeconds();
      return b;
         }
		},
		methods:{
          getMyTeamList(){
          	
          	getMyTeamList(this.params).then(res=>{
          		this.teamList=res.data;
          		this.total=res.total;
              res.data.map(item=>{
                if(item.level==0){
                  item.levelName="非会员"
                }else if(item.level==1){
                  item.levelName="vip会员";
                }else if(item.level==2){
                  item.levelName="商家";
                }else if(item.level==3){
                  item.levelName="市代"
                }else if(item.level==4){
                  item.levelName="省代"
                }
              })
          	})
          }
		}
	}



